.App {
  text-align: center;
}

body{
  background-image: linear-gradient(to right, rgb(217, 85, 221), rgb(79, 1, 153))
}

.mon{font-family: 'Bigelow Rules';
font-size: 75px;
color: rgb(219, 153, 10);
margin-bottom: 20px;
margin-top: 20px;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.search{
  margin-bottom: 35px;
  height: 30px;
  width: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
